
import { defineComponent, PropType } from 'vue';
import { useI18n } from 'vue-i18n';
import { useStore } from 'vuex';
import { AppError, ArchiveType, ToDoList } from '@/types/shared';
import TodoItem from '@/components/todo/TodoItem.vue';
import { DropdownItem } from '@/types/components';
import Dropdown from '@/components/Dropdown.vue';
import List from '@/components/list/List.vue';
import ListItem from '@/components/list/ListItem.vue';
import { getLocalDate } from '@/services/utils/time';

const DELETE_OPTION = 'Delete';

export default defineComponent({
  components: {
    TodoItem,
    Dropdown,
    List,
    ListItem,
  },
  props: {
    list: {
      type: Object as PropType<ToDoList>,
      required: true,
    },
  },
  setup(props) {
    const { t } = useI18n();
    const store = useStore();
    const dateTitle = getLocalDate(props.list.startDate || 0) || t('todos.title');
    const options: DropdownItem[] = [{
      value: DELETE_OPTION,
      name: t(`todos.options.${DELETE_OPTION}`),
      disabled: false,
    }];

    const onSelectOption = async () => {
      const result = await store.dispatch('archives/delete', {
        id: props.list.id,
        type: ArchiveType.TodoList,
      });

      if (result instanceof AppError) {
        console.log(result);
      }
    };

    return {
      dateTitle,
      options,
      onSelectOption,
    };
  },
});
