<template>
  <div class="archive-list">
    <page-title>
      {{t('archiveListPage.title')}}
      <template #sub>{{t('archiveListPage.pageDescription')}}</template>
    </page-title>
    <div v-if="achives.todoList && achives.todoList.length" class="archive-list__block">
      <todo-archive v-for="list in achives.todoList" :list="list" :key="list.id"/>
    </div>
    <card v-else flat class="habits__empty">
      <template #body>
        <alert v-if="error" variant="error" :closeable="false">{{t('habitsPage.error')}}</alert>
        <p v-else class="text-lg">{{t('habitsPage.empty')}}</p>
      </template>
    </card>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, watch } from 'vue';
import { useI18n } from 'vue-i18n';
import { useStore } from 'vuex';
import PageTitle from '@/components/PageTitle.vue';
import { AppError, Archive, ErrorCode } from '@/types/shared';
import TodoArchive from '@/components/todo/TodoArchive.vue';
import Card from '@/components/Card.vue';
import Alert from '@/components/Alert.vue';

export default defineComponent({
  components: {
    PageTitle,
    TodoArchive,
    Card,
    Alert,
  },
  setup() {
    const { t } = useI18n();
    const store = useStore();
    const achives = ref<Archive| Record<string, unknown>>({});
    const error = ref<AppError | null>(null);

    watch(() => store.state.archives.archive, () => {
      achives.value = store.state.archives.archive;
    });

    store.dispatch('archives/fetch').then((response: unknown) => {
      if (response instanceof AppError && !ErrorCode.NotFound) {
        error.value = response;
      }
    });

    return {
      t,
      error,
      achives,
    };
  },
});
</script>

<style lang="scss">
@import '@/assets/styles/utils/_mixins.scss';

.archive-list {
  &__block {
    .list {
      @apply mt-2 mb-6;
      @include miniContentSize;
    }
  }
}
</style>
