<template>
  <div class="todo-list">
    <list>
      <list-item is-title>
        <span>{{dateTitle}}</span>
        <div class="todo-list__menu">
          <div class="todo-list__options">
            <dropdown :items="options" position="right" @selected="onSelectOption" />
          </div>
        </div>
      </list-item>
      <list-item v-for="item in list.todos" :key="item.key">
        <todo-item
          :todo="item"
          :isArchive="true"
          :isEditMode="false"
          :isForToday="false"
        >
          {{item.description}}
        </todo-item>
      </list-item>
    </list>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';
import { useI18n } from 'vue-i18n';
import { useStore } from 'vuex';
import { AppError, ArchiveType, ToDoList } from '@/types/shared';
import TodoItem from '@/components/todo/TodoItem.vue';
import { DropdownItem } from '@/types/components';
import Dropdown from '@/components/Dropdown.vue';
import List from '@/components/list/List.vue';
import ListItem from '@/components/list/ListItem.vue';
import { getLocalDate } from '@/services/utils/time';

const DELETE_OPTION = 'Delete';

export default defineComponent({
  components: {
    TodoItem,
    Dropdown,
    List,
    ListItem,
  },
  props: {
    list: {
      type: Object as PropType<ToDoList>,
      required: true,
    },
  },
  setup(props) {
    const { t } = useI18n();
    const store = useStore();
    const dateTitle = getLocalDate(props.list.startDate || 0) || t('todos.title');
    const options: DropdownItem[] = [{
      value: DELETE_OPTION,
      name: t(`todos.options.${DELETE_OPTION}`),
      disabled: false,
    }];

    const onSelectOption = async () => {
      const result = await store.dispatch('archives/delete', {
        id: props.list.id,
        type: ArchiveType.TodoList,
      });

      if (result instanceof AppError) {
        console.log(result);
      }
    };

    return {
      dateTitle,
      options,
      onSelectOption,
    };
  },
});
</script>

<style lang="scss" scoped>
@import "./TodoItems.scss";
</style>
